import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'

const cityUrl = "https://restaurentappapi.herokuapp.com/city";
const restaurentUrl = "https://restaurentappapi.herokuapp.com/restaurent?city="
class SearchSection extends Component {
    constructor() {
        super()

        this.state = {
            city: '',
            restaurents: ''
        }
    }

    renderCity = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item.city} key={item.city}>
                        {item.city_name}
                    </option>
                )
            })
        }
    }
    renderRestaurent = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item._id} key={item._id}>
                        {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }

    handleCity = (event) => {
        const cityid = event.target.value
        const rrurl = `${restaurentUrl}${cityid}`
        fetch(rrurl, { Method: 'GET' })
            .then((res) => res.json())
            .then((data) => { this.setState({ restaurents: data }) })

    }
    handlerestaurent = (event) => {
        console.log(this.state.restaurents)
        this.props.history.push(`/detail/${event.target.value}`)
    }
    render() {
        return (
            <div className="header">
                <div className="background-image ">
                    <div className="main-logo">
                        <a href="#"><img src={'/img/logo.png'} alt="logo" /></a>
                    </div>
                    <div className=" main-heading ">Find the best restaurants, cafés, and bars</div>
                    <div style={{ marginTop: 30 }}>
                        <div className=" col-md-3  col-sm-5  col-md-offset-3 col-sm-offset-1">
                            <div className="form-group">

                                <select className="form-control" onChange={this.handleCity}>
                                    <option value="">----select location----</option>
                                    {this.renderCity(this.state.city)}
                                </select>
                            </div>
                        </div>
                        <div className="form-group col-md-3 col-sm-5 ">
                            <select className="form-control" onChange={this.handlerestaurent}>
                                <option value="">----select restaurent----</option>
                                {this.renderRestaurent(this.state.restaurents)}
                            </select>
                        </div>

                    </div>

                </div>
            </div>
        )
    }

    componentDidMount() {
        fetch(cityUrl, { Method: 'GET' })
            .then((res) => res.json())
            .then((data) => { this.setState({ city: data }) })
    }
}


export default withRouter(SearchSection);