import React, { Component } from 'react';
import QuickSearchDisp from './QuickSearchDisp'
const ApiUrl = "https://restaurentappapi.herokuapp.com/mealtype";
class QuickSearch extends Component {
    constructor() {
        super()
        this.state = {
            mealtype: ''
        }
    }

    render() {
        return (
            <QuickSearchDisp mealtype={this.state.mealtype} />
        )
    }

    componentDidMount() {
        fetch(ApiUrl, { method: 'GET' })
            .then((res) => res.json())
            .then((data) => {
                this.setState({ mealtype: data })
            })
    }
}

export default QuickSearch;