import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from '../component/homePage/home';
import Searchpage from './listing page/searchpage'
import BookingDisplay from './booking/bookingdisplay'
import Navbar from './navbar';
import BookingForm from './booking/bookingForm'
import DetailPage from './detail page/detailPage'



const Routing = () => {
    return (
        <BrowserRouter>
            <div className="container">
                <Navbar />
                <Route exact path="/" component={Home}></Route>
                <Route path="/list/:id" component={Searchpage}></Route>
                <Route path="/detail/:id" component={DetailPage}></Route>
                <Route path="/booking" component={BookingDisplay}></Route>
                <Route path="/placeorder/:id" component={BookingForm}></Route>
                <footer>
                    <div className="bodyhr" style={{ marginLeft: '12px', width: '100%' }}></div>
                    <center>omy&copy;2020</center></footer>
            </div>
        </BrowserRouter>
    )
}

export default Routing;