import React, { Component } from 'react'
import { Link } from 'react-router-dom'
const ApiUrl = "https://restaurentappapi.herokuapp.com/restaurentdetails/"
const PostUrl = "https://restaurentappapi.herokuapp.com/postorder"
class bookingForm extends Component {
    constructor() {
        super()
        this.state = {
            bookingId: Math.floor(Math.random() * 100000),
            restaurentname: '',
            name: '',
            phonenumber: '',
            address: '',
            item: '1',
            dish: 'Pizza',
            UserId: '',
            status: false
        }
    }
    handleName = (event) => {
        this.setState({ name: event.target.value })
    }
    handlePhone = (event) => {
        this.setState({ phonenumber: event.target.value })
    }
    handleAddress = (event) => {
        this.setState({ address: event.target.value })
    }
    handleItem = (event) => {
        this.setState({ item: event.target.value })
    }
    handleDish = (event) => {
        this.setState({ dish: event.target.value })
    }
    handleSubmit = () => {

        fetch(PostUrl,
            {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(this.state)
            })
            .then((this.props.history.push('/booking?message=Success')))
    }
    render() {
        if (this.state.restaurentname)
            return (
                <React.Fragment>
                    <>
                        <div className="form-group">
                            <label>Order Id:</label>
                            <input readOnly className="form-control" value={this.state.bookingId} />
                        </div>
                        <div className="form-group">
                            <label for="pwd">Restaurent Name:</label>
                            <input readOnly className="form-control" value={this.state.restaurentname} />
                        </div>
                        <div className="form-group">
                            <label>Name:</label>
                            <input type="text" className="form-control" value={this.state.name} onChange={this.handleName} />
                        </div>
                        <div className="form-group">
                            <label>Phone No.:</label>
                            <input className="form-control" type="number" value={this.state.phonenumber} onChange={this.handlePhone} />
                        </div>
                        <div className="form-group">
                            <label >Address:</label>
                            <input className="form-control" value={this.state.address} onChange={this.handleAddress} />
                        </div>

                        <label >Choose Your Favorate Dish:</label>
                        <div className="form-group">
                            <select className="form-control" value={this.state.dish} onChange={this.handleDish}>
                                <option value="Pizza">Pizza</option>
                                <option value="Berger">Berger</option>
                                <option value="Meggi">Meggi</option>
                                <option value="chineise">chineise</option>
                            </select>
                        </div>
                        <label >Quantity:</label>
                        <div className="form-group">
                            <select className="form-control" value={this.state.item} onChange={this.handleItem}>
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                            </select>
                        </div>
                        <center>
                            <Link to={`/detail/${this.props.match.params.id}`}><button type="submit" className="btn btn-danger" style={{ marginRight: '20px' }}>cancle </button></Link>
                            <button type="submit" className="btn btn-success" onClick={this.handleSubmit}>place Order</button>
                        </center>
                    </>
                </React.Fragment>
            )
        else {
            return (
                <center> <img src="https://media.giphy.com/media/3o7bu8sRnYpTOG1p8k/giphy.gif" className="lodingImg img-responsive" /></center>
            )
        }
    }
    componentDidMount() {
        this.setState({ UserId: localStorage.getItem('userId') })
        let restId = this.props.match.params.id;
        fetch(`${ApiUrl}${restId}`, { method: 'GET' })
            .then((res) => res.json())
            .then((data) => { this.setState({ restaurentname: data[0].name }) })
    }
}
export default bookingForm;