import React, { Component } from 'react';
const ApiUrl = "https://restaurentappapi.herokuapp.com/orders"

class BookingDisplay extends Component {
    constructor() {
        super()
        this.state = {
            order: '',
        }
    }

    renderBooking = (data) => {
        if (data)
            return data.map((item) => {
                return (
                    <tr key={item.bookingId}>
                        <td>{item.bookingId}</td>
                        <td>{item.name}</td>
                        <td>{item.restaurentname}</td>
                        <td>{item.dish}</td>
                        <td>{item.item}</td>
                        <td>{item.phonenumber}</td>
                        <td>{item.address}</td>
                        <td style={{ paddingLeft: '35px' }}>{item.status ? < img src='img/approved.png' /> : <img src='img/placed.png' />}</td>
                    </tr>)
            })
        else {
            return (
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td><img src="https://media.giphy.com/media/3o7bu8sRnYpTOG1p8k/giphy.gif" className="loadingBooking img-responsive" alt="loading...." /></td>
                </tr>
            )
        }
    }
    render() {
        let Url = `${ApiUrl}?user=${localStorage.getItem('userId')}`
        fetch(Url, { mthod: 'GET' })
            .then((res) => res.json())
            .then((data) => {
                this.setState({ order: data })
            })
        return (
            <React.Fragment>
                <table id='table' className="table table-hover">
                    <thead>
                        <tr>
                            <th>Order Id</th>
                            <th>Person Name</th>
                            <th>Hotel Name</th>
                            <th>Dish Name</th>
                            <th>Quantity</th>
                            <th>Phone no.</th>
                            <th>Address</th>
                            <th>Order Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.renderBooking(this.state.order)}
                    </tbody>
                </table>
            </React.Fragment>
        )
    }

}
export default BookingDisplay;